
.subscribe-dialog-wrapper {
  position: relative;
  box-sizing: border-box;
  max-width: 100%;
  margin: 0 0.2rem;
  padding: 0.4rem 0.2rem 0.2rem 0.2rem;
  background-color: #f6f6f6;
  border-radius: 4px;
}
.subscribe-dialog-wrapper .close {
  position: absolute;
  top: 0.1rem;
  right: 0.1rem;
  font-size: 0.25rem;
  color: #a2a8af;
}
.subscribe-dialog-wrapper .title {
  font-size: 17px;
  color: #222c36;
}
.subscribe-dialog-wrapper .desc {
  line-height: 20px;
  font-size: 13px;
  color: #c0c8d1;
}
.subscribe-dialog-wrapper .button {
  display: inline-block;
  width: 230px;
  margin-top: 20px;
  padding: 14px;
  border-radius: 4px;
  font-size: 15px;
}
.subscribe-dialog-wrapper .content {
  max-width: 100%;
  min-height: 4rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}
.subscribe-dialog-wrapper .content .cube-form {
  width: 100%;
  margin: 0.1rem 0;
}
.subscribe-dialog-wrapper .content img {
  max-width: 3.4rem;
  max-height: 4rem;
}
.subscribe-dialog-wrapper .content p {
  width: 100%;
  font-size: 0.16rem;
  margin-bottom: 0.1rem;
  line-height: 1.3;
  font-weight: 600;
}


