
.cube-tab-bar {
  position: absolute;
  background: #f0f0f0;
  z-index: 40;
  bottom: 0;
  width: 100%;
  border-top: 1px solid #d0d0d0;
  font-size: 0.17rem;
}
.cube-tab {
  position: relative;
  z-index: 50;
}
.tab-bar-view .section {
  margin-bottom: 10px;
  background-color: #fff;
}
.tab-bar-view .option-list .group {
  margin-bottom: 15px;
}
.tab-bar-view .option-list .item {
  height: 52px;
  border: 1px solid rgba(0,0,0,0.1);
  background-color: #fff;
  border-radius: 5px;
}
.tab-bar-view .cube-tab-bar {
  background-color: #fff;
}


